import React from "react";
import { Card, Table, Spin } from "antd";
import { LoadingOutlined } from '@ant-design/icons';

import { api_base_url, method_post } from '../../util/config'

const columns = [
    { title: 'ID', dataIndex: 'id', key: 'full_name' },
    { title: 'Name', dataIndex: 'full_name', key: 'full_name' },
    { title: 'DOB', dataIndex: 'dob', key: 'dob' },
    { title: 'Gender', dataIndex: 'gender', key: 'gender' },
    { title: 'Email', dataIndex: 'email', key: 'email' },
    { title: 'View', dataIndex: '', key: 'x', render: () => <span className="gx-link">View</span> },
    { title: 'Edit', dataIndex: '', key: 'x', render: () => <span className="gx-link">Edit</span> },
    { title: 'Delete', dataIndex: '', key: 'x', render: () => <span className="gx-link">Delete</span> },
];

class Customers extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            customers_data: [],
            isLoading: true
        };
    }

    componentDidMount() {
        fetch(api_base_url + "listAllCustomer", method_post).then(response => response.json())
            .then(response => {
                this.setState({ customers_data: response.data, isLoading: false })
            })
            .catch();
    }

    render() {
        const { customers_data, isLoading } = this.state;
        const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />

        return (
            <div>
                {isLoading ? <Spin indicator={antIcon} /> :
                    <Card title="Customers">
                        <Table className="gx-table-responsive"
                            columns={columns}
                            dataSource={customers_data}
                        />
                    </Card>
                }
            </div>
        );
    }
}

export default Customers;
